body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

html, body, #root, .application {
    height: 100%;
}

.application {
    margin: 0 1rem;
}
.editor {
    display: flex;
    height: 35rem;
    margin-bottom: 2rem;
}

.editor__textarea {
    flex: 1 1;
    padding: 0 0.5rem;
}
.editor__textarea:first-child {
    padding-left: 0;
}
.editor__textarea:last-child {
    padding-right: 0;
}

.editor__textarea label {
    display: block;
}

.editor__textarea textarea {
    width: 100%;
    height: 100%;
    font-size: 1rem;
}

